import React from "react";
import { Router, Switch, Route, Redirect } from "react-router-dom";
import { createHashHistory } from "history";
import Login from "./Components/Login";
import Main from "./Layouts/Main";
import API from "./Services/API";
import SignIn from "./Layouts/SignIn";
import Preferences from "./Utils/Preferences";

export default class App extends React.Component {
    state = { user: null as LoggedIn }
    history = createHashHistory();
    constructor(props: any) {
        super(props);
        if (props.user) {
            this.state.user = props.user;
            API.KEY = props.user.token;
        }
    }
    handleLoggedIn = (user: LoggedIn) => {
        this.setState({ user });
        // aun esta pendiente, por lo que tiene que ir directamente al curso
        if (user.estado === "PENDIENTE")
            return this.history.push("/classroom");
        this.history.push("/app");
        API.KEY = user.token;
        try {
            Preferences.setPrefix(user.id && (user.id + "_"));
        } catch (ex) {
            console.info("Ocurrio un error en Preferences.setPrefix", ex);
        }
    }
    render() {
        const { user } = this.state;
        var user_routes;
        if (user) {
            switch (user.estado) {
                case "PENDIENTE":
                    user_routes = <Route>BIENVENIDO A CLASSROOM</Route>;
                    break;
                case "RECHAZADO":
                    user_routes = <Route>USUARIO ESTÁ DESHABILITADO</Route>;
                    break;
                default:
                    user_routes = (<Switch>
                        <Route path="/classroom">BIENVENIDO A CLASSROOM</Route>
                        <Route path="/app" render={() => <Main {...user} />} />
                        <Route>
                            <Redirect to="/app" />
                        </Route>
                    </Switch>);
                    break;
            }
        }
        return (<Router history={this.history} >
            {user
                ? user_routes
                : (<Switch>
                    <Route path="/signin" component={SignIn} />
                    <Route path="/recover-password">AQUI PUEDE RECUPERAR TU CONTRAEÑA</Route>
                    <Route path="/login" render={_ => <Login onLoggedIn={this.handleLoggedIn} />} />
                    <Route>
                        <Redirect to="/login" />
                    </Route>
                </Switch>)
            }
        </Router>)
    }
}