import React from 'react';
import { Drawer, List, ListItem, ListItemIcon, ListItemText, Typography, CssBaseline, withStyles, AppBar, Toolbar, IconButton, Hidden } from '@material-ui/core';
import { ShoppingCart, ListAlt, Menu, Close, Help, Inbox, Report, Category/*, Policy, PeopleAlt*/ } from "@material-ui/icons";
import { Link, Switch, Route, Redirect, HashRouter } from "react-router-dom";
import Order from './../Components/Order';
import OrderList from './../Components/OrderList';
import Feed from './../Components/Feed';
import Reports from './../Components/Reports';
import OrderQueue from '../Components/OrderQueue';
import Products from '../Components/Products';

const OPCIONES = [
  { label: "Inicio", Icon: Inbox, path: "/feed" }
  /** NINGUN PATH puede comenzar por order porque dana algunas partes de la logica */
  , { label: "Nuevo Pedido", Icon: ShoppingCart, path: "/order" }

  , { label: "Pedidos", Icon: ListAlt, path: "/my-orders" }
  , { label: "Pedidos", Icon: ListAlt, path: "/queue" }

  //,{ label: "Politicas", Icon: Policy, path: "/policy" }

  , { label: "Catálogo", Icon: Category, path: "/products" }
  //,{ label: "Usuarios", Icon: PeopleAlt, path: "/users" }

  , { label: "Reportes", Icon: Report, path: "/reports" }
];

const drawerWidth = 200;

export default withStyles(theme => ({
  drawer: {
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
  },
  content: {
    flexGrow: 1,
    [theme.breakpoints.up('sm')]: {
      marginLeft: drawerWidth
    }
  },
  appBar: {
    display: "block"
  }
}))(class Main extends React.Component<MainProps, any> {

  state = {
    selected: ""
    , editarPVPsdurantePedido: false
    , isMenuOpen: false
    , username: ""
    , grupo: ""
    , token: ""
    , rol: 0
    , accesos: []
    , descuentos: [] as Descuento[]
  };

  constructor(props: any) {
    super(props);
    this.onMounted = this.onMounted.bind(this);
    this.handleMenu = this.handleMenu.bind(this);
    this.closeMenu = this.closeMenu.bind(this);
    this.state.username = props.username;
    this.state.grupo = props.grupo;
    this.state.rol = props.rol;
    this.state.descuentos = props.descuentos || [];
    this.state.editarPVPsdurantePedido = props.editarPVPsdurantePedido;
    this.state.accesos = [...OPCIONES];
    if (this.state.rol === 3) {
      this.state.accesos.splice(2, 1);
    } else if (this.state.rol === 2) {
      this.state.accesos = [OPCIONES[0], OPCIONES[1], OPCIONES[2], OPCIONES[5]];
    } else {
      this.state.accesos = [OPCIONES[0], OPCIONES[1], OPCIONES[2], OPCIONES[4], OPCIONES[5]];
    }
  }
  onMounted(selected: string) {
    this.setState({ selected });
  }
  handleMenu() {
    this.setState(prev => {
      const state = { ...prev };
      state.isMenuOpen = !state.isMenuOpen;
      return state;
    });
  }
  closeMenu() {
    this.setState({ isMenuOpen: false });
  }
  render() {
    const { selected, isMenuOpen, username, grupo } = this.state;
    const { window, classes } = this.props;
    const container = window !== undefined ? () => window().document.body : undefined;

    const drawerChildren = (<div style={{ display: "flex", flexDirection: "column" }}>
      <div style={{ textAlign: "center", marginTop: 10, marginBottom: 10 }}>
        <Typography style={{ display: "block" }} variant="button">{username}</Typography>
        <Typography style={{ display: "block" }} variant="caption">{grupo}</Typography>
      </div>
      <List style={{ flex: 1 }}>
        {this.state.accesos.map(o => (<ListItem onClick={this.closeMenu} selected={selected === o.path} component={Link} to={o.path} button key={o.label}>
          <ListItemIcon >{<o.Icon />}</ListItemIcon>
          <ListItemText style={{ flex: 1 }} primary={o.label} />
        </ListItem>))}
      </List>
      <ListItem component="a" target="_a" href="https://wa.me/" >
        <ListItemIcon> <Help /> </ListItemIcon>
        <ListItemText primary="AYUDA" />
      </ListItem>
    </div>);

    return (<HashRouter basename="/app">
      <div style={{ width: "100%", height: "100%", display: "flex", flexDirection: "column" }}>
        <CssBaseline />
        <nav className={classes.drawer}>
          <Hidden smUp implementation="css" >
            <Drawer
              container={container}
              variant="temporary"
              anchor={'left'}
              open={isMenuOpen}
              onClose={this.closeMenu}
              classes={{ paper: classes.drawerPaper }}
              ModalProps={{
                keepMounted: true, // Better open performance on mobile.
              }}
            >
              <div className={classes.toolbar} style={{ textAlign: "right" }}>
                <IconButton onClick={this.handleMenu}>
                  <Close />
                </IconButton>
              </div>
              {drawerChildren}
            </Drawer>
          </Hidden>
          <Hidden xsDown implementation="css">
            <Drawer
              classes={{ paper: classes.drawerPaper }}
              variant="permanent"
              open
            >
              {drawerChildren}
            </Drawer>
          </Hidden>
        </nav>

        <Hidden smUp implementation="css" >
          <AppBar position="fixed" className={classes.x}>
            <Toolbar>
              <IconButton
                color="inherit"
                aria-label="open drawer"
                edge="start"
                onClick={this.handleMenu}
              >
                <Menu />
              </IconButton>
              <Typography style={{ flexGrow: 1 }}>{(OPCIONES.find(x => x.path === selected) || {}).label}</Typography>
            </Toolbar>
          </AppBar>
        </Hidden>

        <main className={classes.content}>
          <Hidden smUp implementation="css" >
            <div className={classes.toolbar}></div>
          </Hidden>
          <Switch>
            <Route path="/feed" render={props => <Feed {...props} onMounted={this.onMounted} grupo={this.state.grupo} username={this.state.username} rol={this.state.rol} />} />
            <Route path="/order" render={_ => <Order onMounted={this.onMounted} rol={this.state.rol} descuentos={this.state.descuentos} editablePrices={this.state.editarPVPsdurantePedido} />} />
            <Route path="/my-orders" render={props => <OrderList {...props} rol={this.state.rol} onMounted={this.onMounted} />} />
            <Route path="/queue" render={props => <OrderQueue {...props} onMounted={this.onMounted} />} />

            <Route path="/policy" >
              <iframe title="Politicas de Natú" style={{
                height: "calc(100% - 10px)",
                width: "100%"
              }} src="https://docs.google.com/document/d/e/2PACX-1vSC-0iC8dbpYgrNhlDyRWAQdwdFM4esg40JLZQS-zg6Koix_w1B86KoGI3YPpq-TdNjoJV0eenbcRKv/pub?embedded=true" />
            </Route>

            <Route path="/users" render={props => <div>USUARIOS</div>} />
            <Route path="/products" render={props => <Products {...props} onMounted={this.onMounted} />} />

            <Route path="/reports" render={props => <Reports {...props} rol={this.state.rol} onMounted={this.onMounted} />} />
            <Route path="/" exact><Redirect to="/feed" /></Route>
          </Switch>
        </main>
      </div>
    </HashRouter>)
  }
});

interface MainProps {
  username: string;
  rol: number;
  grupo: string;
  editarPVPsdurantePedido?: boolean;

  token: string;
  window?: any;
  classes: any;
}