import React from "react";
import { Bar } from "react-chartjs-2";
import { Card, CardContent, CardHeader } from "@material-ui/core";

export default class BardCard extends React.PureComponent<{ data: any[], title: string }> {
    state = {
        options: {
            title: { display: false },
            legend: { display: false },
            maintainAspectRatio: false,
            scaleBeginAtZero: true,
            scales: {
                yAxes: [{
                    ticks: {
                        beginAtZero: true
                    }
                }]
            }
        }
    }
    render() {
        var labels = [];
        var values = [];
        for (let i = 0; i < this.props.data.length; i++) {
            const el = this.props.data[i];
            labels.push(el.label);
            if (el.we) {
                values.push(new Date(el.we).toLocaleDateString());
            } else {
                values.push(el.value);
            }
        }
        const data = {
            labels,
            datasets: [{
                backgroundColor: "#3f51b5",
                borderColor: "#3f51b5",
                data: values,
                fill: false
            }]
        }
        return (<Card className="dashboard-card" style={{ display: "flex", flexDirection: "column" }} >
            <CardHeader style={{ textAlign: "center" }} title={this.props.title} />
            <CardContent style={{ width: "100%", flex: 1, padding: 5 }}>
                <Bar options={this.state.options} data={data} />
            </CardContent>
        </Card >)
    }
}