import React from "react";
import { Card, CardContent, Typography, CardHeader, List, ListItem, ListItemText, ListItemSecondaryAction } from "@material-ui/core";

export default function ProfitCard(props: { month: number, year: number }) {
    const { month, year } = props;
    var reservas = 0, promo = 0;
    if (month) {
        reservas = (month * 5) / 100;
        promo = (month * 14) / 100;
    }
    return (<Card className="dashboard-card">
        <CardHeader title="GANANCIAS" style={{ textAlign: "center" }} />
        <CardContent>
            <div style={{ display: "flex", flexDirection: "row" }}>
                <div style={{ textAlign: "center", flex: 1 }}>
                    <Typography color="primary" variant="h5" style={{ flex: 1 }}>$ {reduceMoney(month)}</Typography>
                    <Typography color="primary">ESTE MES</Typography>
                </div>
                <div style={{ borderLeft: "1px solid black", textAlign: "center", flex: 1 }}>
                    <Typography variant="h5" style={{ flex: 1 }} >$ {reduceMoney(year)}</Typography>
                    <Typography>ESTE AÑO</Typography>
                </div>
            </div>
            <Typography variant="subtitle1" style={{ marginTop: 20, paddingLeft: 15, textAlign: "left" }}>Te aconsejamos apartar lo siguiente:</Typography>
            <List>
                <ListItem dense>
                    <ListItemText primary="RESERVAS 5%" />
                    <ListItemSecondaryAction>$ {reservas.toFixed(2)}</ListItemSecondaryAction>
                </ListItem>
                <ListItem dense>
                    <ListItemText primary="PROMOCION 14%" />
                    <ListItemSecondaryAction>$ {promo.toFixed(2)}</ListItemSecondaryAction>
                </ListItem>
            </List>
        </CardContent >
    </Card >)
}
function reduceMoney(value) {
    if (!value) return 0;
    if (value > 1000) return (+(value / 1000).toFixed(1)) + " K";
    if (value > 1000000) return (+(value / 1000).toFixed(1)) + " M";
    return +value.toFixed(2);
}