import React from "react";
import { Dialog, DialogContent, DialogTitle, Typography, TextField, Button, CircularProgress, DialogActions, Divider, FormControlLabel, Checkbox, CardMedia } from "@material-ui/core";
import ConfirmOrder from "../Order/ConfirmOrder";
import API from "../../Services/API";
import { DocToString } from "../../Utils/Calcs";
import OrderActions from "../../Services/OrderActions";

export default class detaOrderDetails extends React.Component<{
    onClose: (refresh: boolean) => void,
    editable?: boolean,
    id: number
}> {
    state = {
        loading: "data",
        _fechaEnvio: "",
        _fechaRecepcion: "",
    } as OrderDetailsState
    componentWillMount() {
        API.GetOrder(this.props.id)
            .then(order => this.setState({ ...order, loading: null }));
    }
    handleClose = (event: React.MouseEvent<HTMLElement>) => {
        this.props.onClose(this.state.shouldRefresh);
    }
    handleButton = (event: React.MouseEvent<HTMLButtonElement>) => {
        const { name } = event.currentTarget;
        if (this.state.loading) return;
        if (name === "APROBAR") {
            let promise = OrderActions.Aprobar(this.state);
            if (!promise) return;
            promise.then(x => this.props.onClose(true))
                .catch(ex => {
                    alert(ex.message);
                    this.setState({ loading: null });
                });
        }
        if (name === "RECHAZAR") {
            let promise = OrderActions.Rechazar(this.state);
            if (!promise) return;
            promise.then(x => this.props.onClose(true))
                .catch(ex => {
                    alert(ex.message);
                    this.setState({ loading: null });
                });
        }
        if (name === "ENVIAR") {
            let promise = OrderActions.Enviar(this.state);
            if (!promise) return;
            promise.then(x => this.props.onClose(true))
                .catch(ex => {
                    alert(ex.message);
                    this.setState({ loading: null });
                });
        }
        if (name === "RECIBIR") {
            let promise = OrderActions.Recibir(this.state);
            if (!promise) return;
            promise.then(x => this.props.onClose(true))
                .catch(ex => {
                    alert(ex.message);
                    this.setState({ loading: null });
                });
        }
        if (name === "FACTURAR") {
            let promise = OrderActions.Facturar(this.state);
            if (!promise) return;
            promise.then(x => this.props.onClose(true))
                .catch(ex => {
                    alert(ex.message);
                    this.setState({ loading: null });
                });
        }
        this.setState({ loading: name });
    }
    handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = event.currentTarget;
        this.setState({ [name]: value });
    }
    handleCheck = (event: React.ChangeEvent<HTMLInputElement>) => {
        const { name, checked } = event.currentTarget;
        this.setState({ [name]: checked });
    }
    render() {
        const { numero,

            fechaCreacion, fechaEnvio, fechaRecepcion,
            detalle,
            descuentoPorcentaje, descuentoTotal, observacion,
            direccion, sector, ciudad, referencia, numeroTransaccion, imgTransaccion,
            estado, total, username,
            survey1, survey2, loading,
            documento, identificacion, razonSocial,
            telefono_factura, direccion_factura, email_factura,
            nombre_contacto, identificacion_contacto, documento_contacto, telefono_contacto,

            numeroFactura, _fechaEnvio, _fechaRecepcion, bienRecibido, observacionRecepcion
        } = this.state;
        const editable = this.props.editable === true;

        if (loading === "data")
            return (<Dialog open={true} onClose={this.handleClose} maxWidth="md" >
                <DialogContent style={{ textAlign: "center", paddingTop: 100, width: 400, height: 400 }}>
                    <CircularProgress size={100} />
                    <Typography variant="h6">CARGANDO...</Typography>
                </DialogContent>
            </Dialog>);


        return (<Dialog open={true} onClose={this.handleClose} maxWidth="md">
            <DialogTitle>PEDIDO #{numero}</DialogTitle>
            <DialogContent>
                <TextRow value={new Date(fechaCreacion).toLocaleString()} label="Fecha" />
                <TextRow value={username} label="Usuario" />
                <TextRow value={estado} label="Estado" />

                {numeroFactura && <TextRow value={numeroFactura} label="Factura #" />}
                {fechaEnvio && <TextRow value={new Date(fechaEnvio).toLocaleDateString()} label="Enviado" />}
                {fechaRecepcion && <TextRow value={new Date(fechaRecepcion).toLocaleDateString()} label="Recibido" />}
                {estado === "RECIBIDO" && <TextRow value={(bienRecibido ? "NO" : "SI")} label="Incompleto" />}
                {estado === "RECIBIDO" && <TextRow label="Comentarios" value={observacionRecepcion} />}

                <SectionHeader>PRODUCTOS</SectionHeader>
                <ConfirmOrder items={detalle} descuento={{ porcentaje: descuentoPorcentaje, valor: descuentoTotal }} total={total} subtotal={total + descuentoTotal} />
                <TextRow value={observacion} label="Observación" />

                <SectionHeader>DATOS DE ENTREGA</SectionHeader>

                <TextRow value={nombre_contacto} label="Nombre" />
                <TextRow value={identificacion_contacto} label={DocToString(documento_contacto as any)} />
                <TextRow value={telefono_contacto} label="Telefono" />

                <TextRow value={ciudad} label="Ciudad" />
                <TextRow value={sector} label="Sector" />
                <TextRow value={direccion} label="Direccion" />
                <TextRow value={referencia} label="Referencia" />


                <SectionHeader>DATOS DE FACTURACIÓN</SectionHeader>
                <TextRow value={identificacion} label={DocToString(documento as any)} />
                <TextRow value={razonSocial} label="Nombre" />
                <TextRow value={direccion_factura} label="Dirección" />
                <TextRow value={telefono_factura} label="Teléfono" />
                <TextRow value={email_factura} label="Email" />

                <SectionHeader>PAGO</SectionHeader>
                {numeroTransaccion && <TextRow value={numeroTransaccion} label="Comprobante" />}
                {imgTransaccion && <CardMedia style={{ height: 400 }} image={imgTransaccion} title="Comprobante" />}

                <SectionHeader>ENCUESTA</SectionHeader>
                <Typography style={{ marginTop: 10 }} variant="caption">Cómo podemos ayudarte a mejorar tus ventas?</Typography>
                <Typography>{survey1 || ""}</Typography>
                <Typography style={{ marginTop: 10 }} variant="caption">
                    Compártenos alguna experiencia que hayan tenido tus clientes usando nuestros productos
                </Typography>
                <Typography>{survey2 || ""}</Typography>

                {estado === "PENDIENTE" && editable && <ActionButtons
                    loadingAction={this.state.loading}
                    label="APROBAR"
                    showReject={true}
                    onClick={this.handleButton}
                />}

                {estado === "APROBADO" && editable && <React.Fragment>
                    <SectionHeader>ENVIAR</SectionHeader>
                    <div style={{ display: "flex", flexDirection: "row", marginTop: 15, marginBottom: 10 }}>
                        <Typography variant="button" style={{ marginRight: 10, paddingTop: 5 }} >Fecha de Envío</Typography>
                        <TextField
                            type="date"
                            fullWidth
                            inputProps={{ id: "_fechaEnvio" }}
                            name="_fechaEnvio"
                            value={_fechaEnvio || ""}
                            style={{ flex: 1 }}
                            onChange={this.handleChange}
                        />
                    </div>
                    <ActionButtons
                        loadingAction={this.state.loading}
                        label="ENVIAR"
                        onClick={this.handleButton}
                    />
                </React.Fragment>}

                {estado === "ENVIADO" && editable && <React.Fragment>
                    <SectionHeader>RECIBIR</SectionHeader>
                    <div style={{ display: "flex", flexDirection: "row", margin: 10 }}>
                        <Typography variant="button" style={{ marginRight: 10, paddingTop: 5 }} >Fecha Recepción</Typography>
                        <TextField
                            type="date"
                            fullWidth
                            name="_fechaRecepcion"
                            value={_fechaRecepcion || ""}
                            inputProps={{ id: "_fechaRecepcion" }}
                            style={{ flex: 1 }}
                            onChange={this.handleChange}
                        /></div>
                    <FormControlLabel
                        label="El pedido se envió completo?"
                        control={<Checkbox
                            checked={bienRecibido}
                            onChange={this.handleCheck}
                            name="bienRecibido"
                        />}
                    />
                    <TextField
                        name="observacionRecepcion"
                        value={observacionRecepcion || ""}
                        onChange={this.handleChange}
                        rows={2}
                        required={!bienRecibido}
                        label="Comentarios"
                        fullWidth
                        multiline
                        inputProps={{
                            id: "observacionRecepcion",
                            maxLength: 300
                        }}
                    />
                    <ActionButtons
                        loadingAction={this.state.loading}
                        name="RECIBIR"
                        label="COMPLETAR"
                        onClick={this.handleButton}
                    />
                </React.Fragment>}

                {estado === "RECIBIDO" && editable && <React.Fragment>
                    <SectionHeader>REGISTRAR FACTURA</SectionHeader>
                    <TextField
                        label="Numero de Factura"
                        placeholder="###-###-#####"
                        value={numeroFactura}
                        inputProps={{
                            id: "numeroFactura",
                            maxLength: 20
                        }}
                        fullWidth
                        onChange={this.handleChange}
                        name="numeroFactura"
                    />
                    <ActionButtons
                        loadingAction={this.state.loading}
                        label="FACTURAR"
                        onClick={this.handleButton}
                    />
                </React.Fragment>}

            </DialogContent>
            <DialogActions>
                <Button onClick={this.handleClose}>SALIR</Button>
            </DialogActions>
        </Dialog>);
    }
}

export interface OrderDetailsState extends Order {
    loading: string;
    _fechaEnvio: string;
    _fechaRecepcion: string;
    shouldRefresh?: boolean;
}

function TextRow(props: { value: any, label: any }) {
    const { value, label } = props;
    return <div style={{ width: "100%", textAlign: "left" }}>
        <div style={{ width: 120, display: "inline-block" }}>
            <Typography variant="body1" >{label}</Typography>
        </div>
        <Typography variant="body2" style={{ width: "auto", display: "inline" }}>{value}</Typography>
    </div>
}

function SectionHeader(props: any) {
    const style = { marginTop: 12, marginBottom: 5 } as any;
    if (props.center) style.textAlign = "center";
    return (
        <div style={style}>
            <Typography variant="h6">{props.children}</Typography>
            <Divider />
        </div>);
}
function ActionButtons(props: {
    loadingAction: string,
    label: string,
    onClick: any,
    showReject?: boolean,
    name?: string
}) {
    return <div style={{ display: "flex", flexDirection: "row" }}>
        <Button
            style={{ margin: 10, flex: 1 }}
            onClick={props.onClick}
            variant="outlined"
            color="primary"
            disabled={Boolean(props.loadingAction)}
            name={props.name || props.label}
        >
            {props.loadingAction === (props.name || props.label)
                ? "CARGANDO..."
                : props.label}
        </Button>
        {props.showReject &&
            (<Button
                style={{ margin: 10, flex: 1 }}
                onClick={props.onClick}
                variant="outlined"
                color="secondary"
                disabled={Boolean(props.loadingAction)}
                name="RECHAZAR"
            >
                {props.loadingAction === "RECHAZAR"
                    ? "CARGANDO..."
                    : "RECHAZAR"}
            </Button>)}
    </div>
}