const DOMAIN = "https://natuapp.ue.r.appspot.com";
//const DOMAIN = "http://localhost:9000";

class API {
    KEY = "";
    Login(body) { return this.F("user/login", { body, method: "POST" }); }
    GetProducts(todos?: boolean) { return this.F("product" + (todos ? "?todos=1" : "")); }
    GetOrders(): Promise<Order[]> { return this.F("order"); }
    GetOrder(id: number): Promise<Order> { return this.F("order/" + id); }
    SaveOrder(body: Order) { return this.F("order", { method: "POST", body }); }
    ActionOrder(id: number | string, body: ActionBody) { return this.F("order/" + id, { method: "PUT", body }); }
    Stats(array_items, semana?, year?) { return this.F("dashboard?data=" + array_items.join(",") + (semana ? ("&semana=" + semana) : "") + (year ? ("&year=" + year) : "")); }
    ProductSalesReport(params: { desde: string, hasta: string, estado: any }) {
        var param_str = "";
        if (params && Object.keys(params).length)
            param_str = "?" + new URLSearchParams(params).toString();

        return this.F("reports/product-sales" + param_str);
    }
    GetGroups() { return this.F("user/groups"); }
    SignIn(body: any) { return this.F("user", { method: "POST", body }); }

    GetUsers() { return this.F("user") }
    GetUser(id: number) { return this.F("user/" + id) }

    SaveProduct(item: FullProduct) {
        return this.F("product" + (item.id ? ("/" + item.id) : ""), {
            method: item.id ? "PUT" : "POST",
            body: item
        });
    }
    DeleteProduct(id: number) {
        return this.F("product/" + id, { method: "DELETE" });
    }

    private F(URI, options: SpecialRequest = {}) {
        const DEFAULTS: any = {
            method: "GET",
            cache: "reload",
            mode: "cors"
        };
        var ops: RequestInit = { ...DEFAULTS, ...options };
        if (!ops.headers) ops.headers = {};
        ops.headers['Content-Type'] = 'application/json';
        ops.headers['Access-Control-Allow-Origin'] = '*';

        if (this.KEY) {
            if (!ops.headers) ops.headers = {};
            ops.headers["Authorization"] = 'Basic ' + this.KEY;
        }

        if (ops.body && (typeof ops.body !== typeof ""))
            ops.body = JSON.stringify(ops.body);

        return fetch(DOMAIN + "/" + URI, ops).then(response => {
            if (response.ok) return response.json();
            return response.clone().json()
                .catch(() => {
                    return response.text().then(x => {
                        throw {
                            message: x,
                            status: response.status,
                            statusText: response.statusText
                        }
                    })
                }).then(obj => {
                    throw {
                        ...obj,
                        status: response.status,
                        statusText: response.statusText
                    }
                })
        }).catch(ex => {
            if (ex.message === "Failed to fetch") {
                throw {
                    message: "Este servicio no se encuentra disponible por el momento. Intentelo más tarde.",
                    status: 0
                }
            }
            if (!ex.message) ex.message = ex.statusText;
            throw ex;
        });
    }
}
export default new API();

interface SpecialRequest extends RequestInit {
    body?: any
}

interface ActionBody {
    action: "SURVEY" | "APROBAR" | "RECHAZAR" | "ENVIAR" | "RECIBIR" | "FACTURAR",
    value?: any,
    bienRecibido?: boolean,
    observacionRecepcion?: string
}