import React from "react";
import { CardMedia, Card, CardContent, Typography, Input, CardActions, InputAdornment, IconButton, CardActionArea } from "@material-ui/core";
import { AttachMoney, Add, Remove } from "@material-ui/icons";

interface Props extends Product {

    id: any;
    pvp_final: any;

    editablePrice?: boolean;
    onActionClick?: (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void;
    onPriceChange?: (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => void;
    hideControls?: boolean;
    onCardClick?: (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void;

    textStyle?: any;
}

export default function (props: Props) {
    const { onCardClick } = props;
    return (<Card>
        {onCardClick
            ? (<CardActionArea name="card" value={props.id} onClick={onCardClick}>
                <Content {...props} />
            </CardActionArea>)
            : <Content {...props} />}
    </Card>);
}

function Content(props: Props) {
    const item = props;
    const textStyle = props.textStyle || {};
    return (<>
        <CardMedia component="img" className="card-product-img" image={item.imgSrc || "https://diegopq95.github.io/take-orders/img/default.png"} />
        <CardContent style={{ display: "flex", flexDirection: "row" }}>
            <Typography style={{ flex: 1, height: 60, overflow: "hidden", ...textStyle }} variant="subtitle1">{item.nombre}</Typography>

            {props.editablePrice
                ? <Input
                    style={{ width: 100 }}
                    type="number"
                    onChange={props.onPriceChange}
                    value={"" + item.pvp_final}
                    name={"" + item.id}
                    startAdornment={<InputAdornment position="start" >
                        <AttachMoney />
                    </InputAdornment>}
                />
                : <Typography variant="h6" style={textStyle}>$ {item.pvp_final.toFixed(2)}</Typography>}

        </CardContent>
        {props.hideControls
            ? null
            : (<CardActions>
                <IconButton onClick={props.onActionClick} name="+" value={item.id}>
                    <Add />
                </IconButton>
                <Typography variant="body1" style={textStyle}>{item.cantidad}</Typography>
                <IconButton disabled={!(item.cantidad > 0)} onClick={props.onActionClick} name="-" value={item.id}>
                    <Remove />
                </IconButton>
            </CardActions>)}
    </>);
}