import React from "react";
import { CardActionArea, CardMedia, Typography } from "@material-ui/core";
import { Image } from "@material-ui/icons";
var COMPONENT_COUNT = 0;
interface Props {
    src: string;
    onChange: (file: File) => void;
    className: string;
    title?: string;
}
export default class ImageInput extends React.PureComponent<Props> {
    COMPONENT_ID = "IMAGE_INPUT_";
    constructor(props) {
        super(props);
        this.COMPONENT_ID += COMPONENT_COUNT;
        COMPONENT_COUNT++;
    }
    handleClick = event => {
        document.getElementById(this.COMPONENT_ID).click();
    }
    handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const files = event.currentTarget.files;
        this.props.onChange((files && files.length && files[0]) || null);
        // clean INPUT
        var input: any = document.getElementById(this.COMPONENT_ID);
        input.type = "text";
        input.value = "";
        input.type = "file";
    }
    render() {
        const { src, title, className } = this.props;
        return (<CardActionArea onClick={this.handleClick} style={{ textAlign: "center" }}>
            <input
                hidden={true}
                type="file"
                onChange={this.handleChange}
                accept="image/*"
                id={this.COMPONENT_ID}
            />
            {src
                ? <CardMedia component="img" className={className} title={title || "Imagen"} image={src} />
                : <div className={className} style={{ display: "table", textAlign: "center", margin: "auto" }} >
                    <div style={{ display: "table-cell", verticalAlign: "middle" }}>
                        <Image />
                        <Typography>Agrega una imagen</Typography>
                    </div>
                </div>}

        </CardActionArea>);
    }
}