import React from "react";
import { Grid } from "@material-ui/core";
import ProductCard from "./ProductCard";

export default function ProductGrid(props: {
items: FullProduct[];
    visible: boolean;

    onActionClick?: any;
    editablePrices?: boolean;
    onPriceChange?: (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => void;

    hideControls?: boolean;
    onCardClick?: (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void;
    showNewItem?: boolean;
}) {
    const visible_style: any = {}
    if (!props.visible) visible_style.display = "none";
    return (<Grid
        container
        alignItems="center"
        style={{ flex: 1, /*overflow: "auto",*/ marginTop: 5, ...visible_style }}
        spacing={3}>
        {props.items.map(item => (
            <Grid item sm={6} md={4} xs={12} key={item.id}>
                <ProductCard
                    {...item}
                    editablePrice={props.editablePrices}
                    onPriceChange={props.onPriceChange}
                    onActionClick={props.onActionClick}
                    onCardClick={props.onCardClick}
                    hideControls={props.hideControls}
                    textStyle={item.habilitado ? null : { textDecoration: "line-through" }}
                />
            </Grid>
        ))}
        {props.showNewItem && <Grid item sm={6} md={4} xs={12}>
            <ProductCard
                id="NUEVO"
                imgSrc="./add.svg"
                pvp_final={0}
                nombre="AGREGA UN NUEVO ITEM"
                onCardClick={props.onCardClick}
                hideControls={true}
            />
        </Grid>}
    </Grid>)
}