import React from "react";
import { List, ListItem, Typography } from "@material-ui/core";

interface Props {
    items: any[],
    subtotal: number,
    total: number,
    show_next_discount?: boolean,
    descuento?: DescuentoCalc,
    LabelDescuentoComponent?: React.ReactElement
}

export default function ConfirmOrder(props: Props) {
    var { items, show_next_discount, descuento, total, subtotal } = props;
    if (!descuento) descuento = { porcentaje: 0, valor: 0 };
    const mostrarDescuentos = show_next_discount && descuento.sgte_descuento_porcentaje;

    return (<List>
        {items.map(item => <Row
            key={item.id}
            col1={item.nombre}
            col2={item.cantidad}
            col3={"$ " + (item.pvp_final * item.cantidad).toFixed(2)}
            componentes={item.componentes}
        />)}

        <Row col1="SUBTOTAL" col3={`$ ${subtotal.toFixed(2)}`} />

        <Row
            col1={props.LabelDescuentoComponent || `TU GANANCIA (${descuento.porcentaje}%) EN ESTE PEDIDO`}
            col3={`$ ${descuento.valor.toFixed(2)}`}
            color="primary"
        />

        <Row col1={"TOTAL"} variant={"subtitle2"} col3={`$ ${total.toFixed(2)}`} />

        {mostrarDescuentos
            && <ListItem>
                <Typography variant="subtitle1" color="secondary">{`Obtén el ${descuento.sgte_descuento_porcentaje}% de descuento agregando ${descuento.sgte_descuento_falta.toFixed(2)} USD más a tu pedido`}</Typography>
            </ListItem>}
    </List>);
}


function Row(props: { col1: any, col2?: string | number, col3: string, color?: any, variant?: any, componentes?: any[] }) {
    const { col1, col2, col3, color, variant, componentes } = props;
    return (<ListItem>
        <div style={{ width: "100%" }}>
            <div>
                <div style={{ width: "100%", display: "table" }}>
                    <Typography color={color || "initial"} style={{ display: "table-cell" }} variant={variant || "body1"}>{col1}</Typography>
                    {col2 && <Typography color={color || "initial"} style={{ display: "table-cell", width: "15%", maxWidth: 100, textAlign: "right" }} variant={variant || "body1"}>{col2}</Typography>}
                    <Typography color={color || "initial"} style={{ display: "table-cell", width: "25%", maxWidth: 100, textAlign: "right" }} variant={variant || "body1"}>{col3}</Typography>
                </div>
            </div>

            {(componentes && componentes.length)
                ? (<div>
                    {componentes.map(c => <Row
                        key={c.id}
                        col1={c.nombre}
                        col2={c.cantidad}
                        col3=""
                        variant="body2"
                        componentes={c.componentes}
                    />)}
                </div>)
                : null}
        </div>
    </ListItem>)
}