export class ValidateInput {
    static tel(value) {
        if (!value) return;
        if (value.length < 7) return "El número de telefono debe tener al menos 7 digitos";
        for (let i = 0; i < value.length; i++) {
            if (!(value[i] === "+" || isDigit(value[i]) || value[i].trim() === "")) {
                return "El número debe tener uno de estos formatos: '09123456789' o '+5931234567890'";
            }
        }
    }
    static email(value) {
        if (!value) return;
        if (value.length < 5) return "El email no está escrito correctamente";
        let twoParts = value.split("@");
        if (twoParts.length !== 2) return "El email no está escrito correctamente";
        if (!(twoParts[1].indexOf(".") >= 0)) return "El email no está escrito correctamente";
        if (value.trim().indexOf(" ") >= 0) return "El email no está escrito correctamente";
        if (value.trim().indexOf("\t") >= 0) return "El email no está escrito correctamente";
        if (value.trim().indexOf("\r") >= 0) return "El email no está escrito correctamente";
    }
}

export function ValidarIdentificacion(value: string, documento: any) {
    if (!value) value = "";
    documento = +documento;
    if (documento === 3) { return; }
    if (documento === 1) {
        if (value.length !== 13) return "Número de RUC debe tener 13 digitos";
        if (value.length === 13 && value === "9999999999999") return;
    }
    if (documento === 2) {
        if (value.length !== 10) return "Número de Cedula debe tener 10 digitos";
    }
    return validadorCedulaRuc(value, false);
}
function validadorCedulaRuc(numero: string, vacioEstaBien?: boolean, exito?: Function, error?: Function) {
    var mensaje = '';
    var esValido = false;
    if (vacioEstaBien && isEmptyOrNull(numero)) {
        mensaje = '';
        esValido = true;
    } else {
        if (isNumber(numero)) {

            if (numero.length < 10) {
                mensaje = "El numero de Cedula debe tener 10 dígitos";
            } else if (numero.length > 10 && numero.length < 13) {
                mensaje = "El numero de RUC debe tener 13 dígitos";
            } else if (numero.length > 13) {
                mensaje = "El numero tiene más de 13 dígitos";
            }


            var suma = 0;
            var residuo = 0;
            var pri = false;
            var pub = false;
            var nat = false;
            var numeroProvincias = 22;
            var modulo = 11;

            /* Verifico que el campo no contenga letras */
            var ok = 1;
            /*for (i=0; i numeroProvincias){
            mensaje = 'El código de la provincia (dos primeros dígitos) es inválido'); esValido = fals;
        } */

            /* Aqui almacenamos los digitos de la cedula en variables. */
            const d1: any = numero.substr(0, 1);
            const d2: any = numero.substr(1, 1);
            const d3: any = numero.substr(2, 1);
            const d4: any = numero.substr(3, 1);
            const d5: any = numero.substr(4, 1);
            const d6: any = numero.substr(5, 1);
            const d7: any = numero.substr(6, 1);
            const d8: any = numero.substr(7, 1);
            const d9: any = numero.substr(8, 1);
            const d10: any = numero.substr(9, 1);
            if (!isDigit(d1)) mensaje = "El digito 1 no es un número";
            if (!isDigit(d2)) mensaje = "El digito 2 no es un número";
            if (!isDigit(d3)) mensaje = "El digito 3 no es un número";
            if (!isDigit(d4)) mensaje = "El digito 4 no es un número";
            if (!isDigit(d5)) mensaje = "El digito 5 no es un número";
            if (!isDigit(d6)) mensaje = "El digito 6 no es un número";
            if (!isDigit(d7)) mensaje = "El digito 7 no es un número";
            if (!isDigit(d8)) mensaje = "El digito 8 no es un número";
            if (!isDigit(d9)) mensaje = "El digito 9 no es un número";
            if (!isDigit(d10)) mensaje = "El digito 10 no es un número";

            if (numero.length > 10) {
                const d11 = numero.substr(10, 1);
                const d12 = numero.substr(10, 1);
                const d13 = numero.substr(10, 1);
                if (d11 && !isDigit(d11)) mensaje = "El digito 11 no es un número";
                if (d12 && !isDigit(d12)) mensaje = "El digito 12 no es un número";
                if (d13 && !isDigit(d13)) mensaje = "El digito 13 no es un número";
            }

            /* El tercer digito es: */
            /* 9 para sociedades privadas y extranjeros */
            /* 6 para sociedades publicas */
            /* menor que 6 (0,1,2,3,4,5) para personas naturales */

            let p1;
            let p2;
            let p3;
            let p4;
            let p5;
            let p6;
            let p7;
            let p8;
            let p9;

            if (d3 == 7 || d3 == 8) {
                mensaje = 'El tercer dígito ingresado es inválido';
                esValido = false;
            }

            /* Solo para personas naturales (modulo 10) */
            if (d3 < 6) {
                nat = true;
                p1 = d1 * 2; if (p1 >= 10) p1 -= 9;
                p2 = d2 * 1; if (p2 >= 10) p2 -= 9;
                p3 = d3 * 2; if (p3 >= 10) p3 -= 9;
                p4 = d4 * 1; if (p4 >= 10) p4 -= 9;
                p5 = d5 * 2; if (p5 >= 10) p5 -= 9;
                p6 = d6 * 1; if (p6 >= 10) p6 -= 9;
                p7 = d7 * 2; if (p7 >= 10) p7 -= 9;
                p8 = d8 * 1; if (p8 >= 10) p8 -= 9;
                p9 = d9 * 2; if (p9 >= 10) p9 -= 9;
                modulo = 10;
            }

            /* Solo para sociedades publicas (modulo 11) */
            /* Aqui el digito verficador esta en la posicion 9, en las otras 2 en la pos. 10 */
            else if (d3 == 6) {
                pub = true;
                p1 = d1 * 3;
                p2 = d2 * 2;
                p3 = d3 * 7;
                p4 = d4 * 6;
                p5 = d5 * 5;
                p6 = d6 * 4;
                p7 = d7 * 3;
                p8 = d8 * 2;
                p9 = 0;
            }

            /* Solo para entidades privadas (modulo 11) */
            else if (d3 == 9) {
                pri = true;
                p1 = d1 * 4;
                p2 = d2 * 3;
                p3 = d3 * 2;
                p4 = d4 * 7;
                p5 = d5 * 6;
                p6 = d6 * 5;
                p7 = d7 * 4;
                p8 = d8 * 3;
                p9 = d9 * 2;
            }

            suma = p1 + p2 + p3 + p4 + p5 + p6 + p7 + p8 + p9;
            residuo = suma % modulo;

            /* Si residuo=0, dig.ver.=0, caso contrario 10 - residuo*/
            const digitoVerificador = residuo == 0 ? 0 : modulo - residuo;

            /* ahora comparamos el elemento de la posicion 10 con el dig. ver.*/
            if (pub == true) {
                if (digitoVerificador != d9) {
                    mensaje = 'El ruc de la empresa del sector público es incorrecto.';
                    esValido = false;
                }
                /* El ruc de las empresas del sector publico terminan con 0001*/
                if (numero.substr(9, 4) != '0001') {
                    mensaje = 'El ruc de la empresa del sector público debe terminar con 0001';
                    esValido = false;
                }
            }
            else if (pri == true) {
                if (digitoVerificador != d10) {
                    mensaje = 'El ruc de la empresa del sector privado es incorrecto.';
                    esValido = false;
                }
                if (numero.substr(10, 3) != '001') {
                    mensaje = 'El ruc de la empresa del sector privado debe terminar con 001';
                    esValido = false;
                }
            }

            else if (nat == true) {
                if (digitoVerificador != d10) {
                    mensaje = 'El número de cédula de la persona natural es incorrecto.';
                    esValido = false;
                }
                if (numero.length > 10 && numero.substr(10, 3) != '001') {
                    mensaje = 'El ruc de la persona natural debe terminar con 001';
                    esValido = false;
                }
            }
            esValido = true;

        } else {
            mensaje = 'Este campo solo admite numeros';
        }
    }

    if (esValido && mensaje == '') {
        if (exito) { exito(); }
        return "";
    } else {
        if (mensaje == '') {
            mensaje = 'La cantidad de digitos no corresponde a Cedula o RUC'
        }
        if (error) { error(mensaje); }
        return mensaje;
    }
}

export function isDigit(char) { return char >= "0" && char <= "9"; }
function isNumber(obj) { return !isNaN(parseFloat(obj)) }
function isEmptyOrNull(obj) {
    if (obj === '' || obj == undefined || obj == null || obj == 'undefined') {
        return true
    } else {
        return false;
    }
}