import React from "react";
import API from "../../Services/API";
import { MonthToString } from "../../Utils/Calcs";
import { RankProps } from "./RankingCard";
export default function (Component) {
    return class extends React.Component<RankWithTimeTravel> {
        state = {
            data: [] as any,
            current_week: null as Week,
            statName: null as any,
            reduceFn: null as (data: any[]) => any[],
            loading: false
        }
        constructor(props) {
            super(props);
            this.state = { ...props };
        }
        handleBack = () => {
            if (this.state.loading) return;
            this.setState({ loading: true });
            var new_week = this.state.current_week.id - 1;
            API.Stats([this.state.statName, "current_week"], new_week)
                .then(this.updateData).catch(this.showError);
        }
        handleNext = () => {
            if (this.state.loading) return;
            this.setState({ loading: true });
            var new_week = this.state.current_week.id + 1;
            API.Stats([this.state.statName, "current_week"], new_week)
                .then(this.updateData).catch(this.showError);
        }
        updateData = (results) => {
            const { current_week, ...remain } = results;
            this.setState({
                data: remain[this.state.statName] || [],
                current_week,
                loading: false
            });
        }
        showError = (ex) => {
            this.setState({ loading: false });
            alert(ex.message);
        }
        render() {
            var { current_week, data, reduceFn, loading } = this.state;
            var subtitle = "";
            if (current_week) {
                let desde = new Date(current_week.start);
                let hasta = new Date(current_week.end);
                subtitle = `${desde.getDate()} ${MonthToString(desde.getMonth())} - ` +
                    `${hasta.getDate()} ${MonthToString(hasta.getMonth())}`;
            }
            if (reduceFn) data = reduceFn(data);

            return (<Component
                {...this.state}
                data={data}
                subtitle={subtitle}
                loading={loading}
                onBackClick={this.handleBack}
                onNextClick={this.handleNext}
            />);
        }
    }
}

interface RankWithTimeTravel extends RankProps {
    statName: string;
    reduceFn?: (data: any[]) => any[];
    current_week?: Week;
}