import React from "react";
import { Tabs, Tab, Paper, Typography, CircularProgress, TextField, Fab } from "@material-ui/core";
import API from "../../Services/API";
import ProductGrid from "../Products/ProductGrid";
import ProductOptions from "./ProductOptions";
import ConfirmOrder from "./ConfirmOrder";
import Data from "./Data";
import Payment from "./Payment";
import { Totals, EstimarDescuento } from "../../Utils/Calcs";
import Survey from "./Survey";
import Preferences from "../../Utils/Preferences";
import { withRouter, Route } from "react-router-dom";
import RouteMessage from "../RouteMessage";
import * as OrderSave from "./OrderSave";
import { Save } from "@material-ui/icons";
import ImageReader, { ImgFileToObject } from "../../Utils/ImageReader";
import ProductSelections from "./ProductSelections";

export default withRouter(class Order extends React.PureComponent<OrderProps, OrderState> {
    state = new OrderState()
    unblockNavigation() { }
    constructor(props: any) {
        super(props);
        this.handleTab = this.handleTab.bind(this);
        this.handleProductAction = this.handleProductAction.bind(this);
        this.handleValueChange = this.handleValueChange.bind(this);
        this.handleDone = this.handleDone.bind(this);

        this.handleChange = this.handleChange.bind(this);

        if (props.onMounted) props.onMounted(props.match.path);
        this.state.tablaDescuentos = (props.descuentos || [])
            .map((d, index) => {
                d.id = d.id || index;
                return d;
            });
    }
    componentDidMount() {
        window.onbeforeunload = _ =>
            this.state.isDirty
                ? "Tu pedido aún está incompleto, deseas salir sin guardarlo?"
                : null;

        this.unblockNavigation = this.props.history.block(this.routerWillLeave.bind(this));

        API.GetProducts()
            .then(productList => {
                this.setState({ productList: productList.map(x => (x.cantidad = 0) === 0 && x), loading: null });
                try {
                    var updates: any = {};

                    const stored_direccion = Preferences.Get("direccion") || {};
                    for (const key in stored_direccion) {
                        if (this.state.hasOwnProperty(key) && stored_direccion[key]) {
                            updates[key] = stored_direccion[key];
                        }
                    }

                    const stored_factura = Preferences.Get("factura") || {};
                    for (const key in stored_factura) {
                        if (this.state.hasOwnProperty(key) && stored_factura[key]) {
                            updates[key] = stored_factura[key];
                        }
                    }

                    const before = Preferences.Get("current_order") || {};
                    for (const key in before) {
                        if (this.state.hasOwnProperty(key)) {
                            updates[key] = before[key];
                        }
                    }
                    if (!(Object.keys(updates).length || (before.choosen && before.choosen.length))) return;
                    //updates.isDirty = Boolean(Object.keys(before).length) || Boolean(before.chooen && before.choosen.lengh);
                    this.setState(updates);
                    if (!before.choosen) return;
                    for (let i = 0; i < before.choosen.length; i++) {
                        const x = before.choosen[i];
                        for (let times = 0; times < x.cantidad; times++) {
                            this.handleProductAction({
                                currentTarget: { name: "+", value: x.id }, avoidIsDirty: true
                            } as any)
                        }
                    }
                } catch (ex) { }
            }).catch(ex => alert(ex.message));
    }
    componentWillUnmount() {
        this.unblockNavigation && this.unblockNavigation();
        window.onbeforeunload && (window.onbeforeunload = undefined);
    }
    handleTab(event: any, currentTab: number) {
        this.setState({ currentTab });
    }
    routerWillLeave(nextLocation?) {
        if (nextLocation && nextLocation.pathname.indexOf(this.props.match.path) === 0) return;
        if (!this.state.isDirty) return;

        var confirm = window.confirm("Tu pedido está incompleto, quieres guardarlo para completarlo después?");
        if (confirm) {
            this.guardarBorrador();
        } else {
            // actualizar todo!
            Preferences.Set("current_order");
        }
    }
    guardarBorrador = (event?) => {
        if (event && !this.state.isDirty) return;
        try {
            OrderSave.GuardarBorrador(this.state);
            if (event) {
                this.props.history.push(this.props.match.path + "/msg?" + new URLSearchParams({
                    a: "Listo",
                    b: "Tu pedido fue guardado como borrador."
                }).toString());
                this.setState({ isDirty: false });
            }
        } catch (ex) { }
    }
    handleDone(event: React.FormEvent) {
        event.preventDefault();
        if (this.state.loading) return;
        this.setState({ loading: "guardar" });

        var error = OrderSave.ValidateSave(this.state);
        if (error) {
            if (error.loading) return;
            if (error.productList) {
                this.failed_msg(error.productList);
                return this.setState({ loading: null });
            }
            var keys = Object.keys(error);
            if (keys.length) {
                this.failed_msg(error[keys[0]], undefined, keys[0]);
                let currentTab = this.state.currentTab;
                if ([
                    "ciudad", "referencia", "direccion", "sector",
                    "identificacion", "documento", "razonSocial",
                    "direccion_factura", "telefono_factura", "email_factura",
                    "identificacion_contacto", "nombre_contacto", "telefono_contacto"
                ].indexOf(keys[0]) >= 0) {
                    currentTab = 2
                }
                return this.setState({ loading: null, currentTab });
            }
        }
        if (this.state.imageFile) {
            return ImgFileToObject(this.state.imageFile)
                .then(imgJson => this._s(imgJson));
        }
        this._s();
    }
    private _s(imgJson?) {
        return OrderSave.Save(this.state, imgJson)
            .then(x => {
                const { id, ...new_state } = x;
                this.setState({ ...new_state, imgTransaccion: null, imageFile: null });
                this.props.history.push(this.props.match.path + "/survey/" + x.id);
            }).catch(ex => {
                this.failed_msg(ex.message + " " + (ex.status || ""), "Ocurrió un error");
                this.setState({ loading: null });
            });
    }
    failed_msg(message, title?, focusafter?) {
        var params = new URLSearchParams({
            a: title || "No se puede guardar",
            b: message,
            focusafter
        });
        this.props.history.push(this.props.match.path + "/msg?" + params.toString());
    }
    handleProductAction(event: React.MouseEvent<HTMLButtonElement>) {
        const { name, value } = event.currentTarget;
        var avoidIsDirty = (event as any).avoidIsDirty;
        this.setState(prev => {
            const state: any = { ...prev };
            state.productList = JSON.parse(JSON.stringify(state.productList));
            var item: Product = state.productList.find((a: any) => a.id === +value);
            var cant: number = (item.cantidad && !isNaN(+item.cantidad) && item.cantidad) || 0;
            const { cantidad_desde } = item;
            if (name === "+") {
                if (item.opciones && item.opciones.length && !avoidIsDirty) {
                    this.props.history.push(this.props.match.path + "/select-product-options/" + item.id);
                    return state;
                }
                if (cant <= 0 && cantidad_desde) {
                    cant = cantidad_desde;
                } else {
                    cant++;
                }
            } else if (name === "-") {
                if (item.opciones && item.opciones.length) {
                    this.props.history.push(this.props.match.path + "/view-product-options/" + item.id);
                    return state;
                }
                if (cant <= 10 && cantidad_desde) {
                    cant = 0;
                } else {
                    cant--;
                }
            }
            item.cantidad = cant;
            state.subtotal = Totals(state.productList.filter(x => x.cantidad)).total;
            // en ROL 3, el descuento es MANUAL, no calculado
            if (this.props.rol === 3) {
                if (state.descuentoAplica)
                    state.descuentoAplica.valor = (state.subtotal * state.descuentoAplica.porcentaje / 100);
            } else {
                state.descuentoAplica = EstimarDescuento(state.subtotal, state.tablaDescuentos);
            }
            if (!avoidIsDirty) state.isDirty = true;
            return state;
        });
    }
    handleValueChange(name: string, value: string) {
        this.setState({ [name]: value, isDirty: true } as any);
    }
    handleChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { value, name } = event.currentTarget;
        this.setState({ [name]: value, isDirty: true } as any);
    }
    handleDescuentoChanged = (event: React.ChangeEvent<HTMLInputElement>) => {
        const { value } = event.currentTarget;
        var descuento = this.state.tablaDescuentos.find(x => x.id === +value);
        if (descuento) {
            var total = Totals(this.state.productList.filter(x => x.cantidad)).total;
            this.setState({
                descuentoAplica: {
                    porcentaje: descuento.porcentaje,
                    valor: (total * descuento.porcentaje / 100),
                    id: descuento.id
                }
            });
        }
    }
    imageChange = (imageFile: File) => {
        if (!imageFile) return;
        document.getElementById("loading-full-message").style.display = "block";
        ImageReader(imageFile)
            .then(imgTransaccion => {
                this.setState({ imgTransaccion, imageFile }, () => {
                    document.getElementById("loading-full-message").style.display = "none";
                });
            }).catch(ex => {
                document.getElementById("loading-full-message").style.display = "none";
                this.failed_msg("No se pudo cargar tu imagen. " + ex.message);
            });
    }
    handleAddSelection = (result?) => {
        if (result) {
            const { id, selections } = result;
            this.setState(prev => {
                const state = { ...prev };
                state.productoSelecciones = JSON.parse(JSON.stringify(state.productoSelecciones));
                if (state.productoSelecciones.hasOwnProperty(id)) {
                    state.productoSelecciones[id].push(selections);
                } else {
                    state.productoSelecciones[id] = [selections];
                }
                state.isDirty = true;
                return state;
            });
        }
        this.props.history.push(this.props.match.path);
    }
    handleDeleteSelection = result => {
        if (result) {
            const { id, index } = result;
            this.setState(prev => {
                const state = { ...prev };
                state.productoSelecciones = JSON.parse(JSON.stringify(state.productoSelecciones));
                if (state.productoSelecciones.hasOwnProperty(id)) {
                    var exists = state.productoSelecciones[id][index];
                    if (exists) state.productoSelecciones[id].splice(index, 1);
                }
                return state;
            });
        }
        this.props.history.push(this.props.match.path);
    }
    handlePriceChange = event => {
        const id = +event.currentTarget.name;
        const pvp_final = event.currentTarget.value;
        this.setState(prev => {
            const state = { ...prev };
            state.productList = [...state.productList];
            var found = state.productList.find(x => x.id === id);
            if (found) {
                found.pvp_final = pvp_final;
                //var foundIndex = state.productList.indexOf(found);
                //state.productList[foundIndex] = { ...found, pvp_final };
            }
            return state;
        });
    }
    render() {
        const {
            currentTab, productoSelecciones, numeroTransaccion, descuentoAplica, subtotal,
            loading, observacion, imgTransaccion, usarImagen, tablaDescuentos,
            productList: originalProductList,
            ...remain_state
        } = this.state;
        var productList = originalProductList.map(x => {
            if (x.opciones && x.opciones.length) {
                x = { ...x, cantidad: 0 };
                var exists = productoSelecciones[x.id];
                // Creo un nuevo articulo, para evitar la Mutación
                if (exists) x.cantidad = exists.length;
            }
            return x;
        });
        const choosenItems = productList.filter((x: any) => x.cantidad);
        console.info("Order render");
        const total = subtotal - ((descuentoAplica && descuentoAplica.valor) || 0);
        if (loading === "articulos") {
            return (<div style={{ width: "100%", textAlign: "center", paddingTop: 100 }} >
                <CircularProgress size={100} />
                <Typography variant="h6">CARGANDO...</Typography>
            </div >);
        }


        return (<div style={{ width: "100%", height: "calc(100% - 1px)", display: "flex", flexDirection: "column", padding: 10, overflow: "hidden" }} >
            <Tabs variant="fullWidth" onChange={this.handleTab} value={currentTab}>
                <Tab label="Productos" />
                <Tab label="Pedido" />
                <Tab label="Datos" />
                <Tab label="Pago" />
            </Tabs>
            {currentTab === 0 && <ProductGrid
                visible={true}
                onActionClick={this.handleProductAction}
                items={productList}
                editablePrices={this.props.editablePrices}
                onPriceChange={this.handlePriceChange}
            />}
            {currentTab === 1 && (<Paper style={{ display: currentTab === 1 ? "block" : "none", textAlign: "center", padding: 10, margin: "auto", marginTop: 10, width: "100%", maxWidth: 500, height: "100%" }}>
                <Typography variant="h5">TU PEDIDO</Typography>
                <ConfirmOrder
                    show_next_discount={true}
                    items={choosenItems}
                    descuento={descuentoAplica}
                    subtotal={subtotal}
                    total={total}
                    LabelDescuentoComponent={this.props.rol === 3
                        ? (<TextField
                            onChange={this.handleDescuentoChanged}
                            value={(descuentoAplica && descuentoAplica.id) || ""}
                            SelectProps={{ native: true }}
                            select
                        >
                            <option>- SELECCIONA UN DESCUENTO -</option>
                            {tablaDescuentos.map(d => (
                                <option key={d.id} value={d.id}>DESCUENTO {d.porcentaje}%</option>
                            ))}
                        </TextField>)
                        : null}
                />
                <TextField variant="outlined" value={observacion} name="observacion" onChange={this.handleChange} style={{ width: "calc(100% - 20px)", margin: 10 }} label="Observacion" multiline rows="2"
                    inputProps={{ maxLength: 300, id: "observacion" }}
                />
            </Paper>)}
            {currentTab === 2 && <Data visible={true} {...remain_state} onValueChange={this.handleValueChange} />}
            {currentTab === 3 && <Payment
                visible={true}
                loading={loading === "guardar"}
                total={total}
                onDone={this.handleDone}
                items={choosenItems}
                numeroTransaccion={numeroTransaccion}
                imgTransaccion={imgTransaccion}
                onImgChange={this.imageChange}
                usarImagen={usarImagen}
                onValueChange={this.handleValueChange}
            />}

            <Route path={this.props.match.path + "/msg"} component={RouteMessage} />
            <Route path={this.props.match.path + "/survey/:id"} component={Survey} />
            <Route path={this.props.match.path + "/select-product-options/:id"}
                children={({ match }) => <ProductOptions
                    showingID={match && +match.params.id}
                    onDone={this.handleAddSelection}
                    productList={productList}
                />}
            />
            <Route path={this.props.match.path + "/view-product-options/:id"}
                children={({ match }) => <ProductSelections
                    id={match && match.params.id}
                    onDeleteSelection={this.handleDeleteSelection}
                    productList={productList}
                    selections={productoSelecciones}
                />}
            />
            <Fab title="Guardar Borrador" onClick={this.guardarBorrador} variant="extended" style={{ position: "fixed", bottom: 20, right: 20 }} color="primary">
                <Save />
            </Fab>
        </div >)
    }
});


export class OrderState {
    currentTab: number = 0;
    productList: Product[] = [];
    productoSelecciones: { [idProducto: number]: number[][] } = {};
    tablaDescuentos: Descuento[] = [];
    descuentoAplica: DescuentoCalc = null;
    subtotal: number = 0;

    position: { lat: number, lng: number } = null;
    direccion: string = "";
    sector: string = "";
    ciudad: string = "";
    referencia: string = "";

    documento: "1" | "2" | "3" = "1";
    razonSocial: string = "";
    identificacion: string = "";
    telefono_factura: string = "";
    direccion_factura: string = "";
    email_factura: string = "";

    documento_contacto: "1" | "2" | "3" = "2";
    identificacion_contacto: string = "";
    nombre_contacto: string = "";
    telefono_contacto: string = "";

    observacion: string = "";
    numeroTransaccion: string = "";
    imgTransaccion = "";
    usarImagen = false;

    loading: "articulos" | "guardar" = null;
    isDirty?: boolean = false;
    imageFile?: Blob;
}

interface OrderProps {
    onMounted: (path: string) => void;
    history: any;
    match: any;
    rol: number;
    editablePrices?: boolean;
    descuentos: Descuento[];
}